import React, {Component} from 'react';
import QrReader from 'react-qr-reader'
import axios from 'axios';
import logo from './logo.svg';
import './App.css';

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rollNumber: '',
            mode: 'details',
            delay: 300,
            result: 'No result',
            attendanceStatus: ''
        }
        this.handleScan = this.handleScan.bind(this);
        this.handleError = this.handleError.bind(this);
        this.renderView = this.renderView.bind(this);
        this.changeRollNumber = this.changeRollNumber.bind(this);
        this.addRollNumber = this.addRollNumber.bind(this);
    }

    handleScan(data) {

        if (data) {
            this.setState({
               attendanceStatus: 'pending'
            });
            axios({
                method: 'get',
                url: `https://attendance-94425.firebaseio.com/attendance/database/users/${this.state.rollNumber}.json?print=pretty`
            })
                .then((res1) => {
                    if(res1.data === null){
                        axios({
                            method: 'patch',
                            url: 'https://attendance-94425.firebaseio.com/attendance/database/users.json',
                            data: {
                                [this.state.rollNumber]: {
                                    "timestamps": {
                                        [data.split('_')[0]]: data
                                    }
                                }
                            },
                        })
                            .then((response) => {
                                if (response.status === 200) {
                                    console.log("Update Success");
                                    this.setState({
                                        attendanceStatus: 'successful'
                                    });
                                }
                            })
                            .catch((response) => {
                                alert(response);
                            });
                    }
                    else{
                        axios({
                            method: 'patch',
                            url: `https://attendance-94425.firebaseio.com/attendance/database/users/${this.state.rollNumber}/timestamps.json`,
                            data: {
                                        [data.split('_')[0]]: data
                            },
                        })
                            .then((response) => {
                                if (response.status === 200) {
                                    console.log("Update Success");
                                    this.setState({
                                        attendanceStatus: 'successful'
                                    });
                                }
                            })
                            .catch((response) => {
                                alert(response);
                            });
                    }
                })


            this.setState({
                result: data,
                mode: 'success',
            })
        }
    }

    handleError(err) {
        console.error(err)
    }

    changeRollNumber(event) {
        this.setState({
            rollNumber: event.target.value
        })
    }

    addRollNumber() {
        this.setState({
            mode: 'scan'
        })
    }

    renderView() {
        switch (this.state.mode) {
            case 'details':
                return <div className="wrap">
                    <input type="text" placeholder='Enter Roll Number' className='roll'
                           onChange={this.changeRollNumber}/>
                    <div onClick={this.addRollNumber} className="rollSubmit">Submit</div>
                </div>;

            case 'scan':
                return <div className="wrap">
                    <div>Attendace for {this.state.rollNumber}</div>
                    <QrReader
                        delay={this.state.delay}
                        onError={this.handleError}
                        onScan={this.handleScan}
                        style={{width: '63%', margin: '2rem auto', border: '3px solid #232322'}}
                    />
                </div>;

            case 'success':
                return <div className="wrap">
                    <div className='data'>QR code data: <b>{ this.state.result }</b></div>
                    <div className='status'>Attendance status: <b>{ this.state.attendanceStatus}</b> </div>

                </div>
        }
    }

    render() {
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">Welcome to React</h1>
                </header>
                {
                    this.renderView()
                }

            </div>
        );
    }
}

export default App;



